import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'searchFilter',
    pure: false
})
export class SearchFilterPipe implements PipeTransform {
    transform(items: any[], searchTech: string): any {
        if (!items || !searchTech) {
            return items;
        }
        return items.filter(item => item.technology.toLowerCase().indexOf(searchTech.toLowerCase()) !== -1 || item.type.toLowerCase().indexOf(searchTech.toLowerCase()) !== -1);
    }
}